<template>
  <div class="ebook text_train"  ref="ebook">
    <div class="title-wrapper" :class="showTy?'input_node-white':'input_node-black'" v-if="isShowBj === 1">
        <div class="left" @click="back()">
            <span class="iconfont icon-back" :class="showTy?'input_node-white':'input_node-black'" style="color:#384150; font-size: 0.5rem;"></span>
        </div>
        <div class='center'>{{bookName}}</div>
        <!-- <div class="right">
            <div class="icon-wrapper">
                <span class="iconfont icon-book" @click="toggleNote()" style="color:#384150; font-size: 0.56rem;"></span>
            </div>
            <div class="icon-wrapper">
                <span class="icon-added iconfont icon-book1" @click="addNote()" style="color:#384150; font-size: 0.42rem;"></span>
            </div>
        </div> -->
    </div>
    <div class="note-wrapper" style="display: none" @click="hideNotes()">
        <div class="note-w">
            <div class="note-menu">书签 <span style="font-weight: normal;">({{bookmark.length}})</span></div>
            <div class="note-item1" v-for="(item, index) in bookmark" :key="index">
                <span class="text1" @click="jumpTo2(item.cfi)">{{item.text}}</span>
            </div>
        </div>
    </div>
    <div >
      <div class="read-wrapper" >
        <div id="reader" class="reader" style='overflow:scroll'></div>
        <div class="ebook-icon-wrapper" style="display: none">
            <div class="ebook-icon" ></div>
        </div>

        <!-- <a id="prev" href="#prev" @click="prevPage()" class="arrow" style="width: 11%;">‹</a>
        <a id="next" href="#next" @click="nextPage()" class="arrow" style="width: 11%; padding-left: 4%;">›</a> -->
        <!-- <div class="n-wrapper" @click="onMaskClick" @touchmove="move" @touchend="moveEnd"></div> -->
        <!--<div id="toggle" class="center" @click="toggleShow()"></div>-->
    </div>
    </div>

    <div>
        <div class="menu-wrapper" v-if="isShowBj === 1" :class="showTy?'input_node-white':'input_node-black'">
          <div class='menu-wrapper_top' >
            <img style="width:0.33rem !important;height:0.33rem;margin-left:0.34rem;display:block" src="../../assets/image/记录@2x.png">
            <!-- <input type="text" :placeholder="bjList.length >= 10?'已到上限10条':'请输入'" class='input_node'  @focus="showAddBjModel" :class="showTy?'':'input_node-black'" :disabled="bjList.length >= 10"> -->
            <div @click="showAddBjModel" style="width: calc(100% - 0.33rem);height:0.33rem;margin-left:0.34rem;display:block" >{{showTip}}</div>
          </div>
          <div class='menu-wrapper_button' style='height:0.9rem;display:flex;width:100%;' v-if="!ifShowBjList">
            <!-- <div class="icon-wrapper">
                <span class="iconfont icon-menu" @click="showSetting(3)" style="color:#384150; font-size: 0.5rem; padding: 0.2rem;"></span>
            </div> -->
            <div class='icon-wrapper' >
              <img v-if="!showTy" src="../../assets/image/目录夜间.png" alt="" class='catalogueIcon' @click="showSetting(3)">
              <img v-if="showTy" src="../../assets/image/目录.png" alt="" class='catalogueIcon' @click="showSetting(3)">
            </div>
            <!-- <div class="icon-wrapper">
                <span class="iconfont icon-progress" @click="showSetting(2)"
                      style="color:#384150; font-size: 0.5rem; padding: 0.2rem;"></span>
            </div> -->
            <!-- <div class="icon-wrapper">
                <span class="iconfont icon-bright" @click="showSetting(1)"
                      style="color:#384150; font-size: 0.4rem; padding: 0.2rem;"></span>
            </div> -->
            <div class='icon-wrapper'>
              <img v-show="showTy" src="../../assets/image/太阳@2x.png" alt="" class='catalogueIcontwo' @click="opentwo(2)">
              <img v-show="!showTy" src="../../assets/image/月亮@2x.png" alt="" class='catalogueIcontwo' @click="opentwo(0)">
            </div>
            <div class='icon-wrapper' >
              <img v-if="showWD" src="../../assets/image/文档我不喜欢.png" alt="" class='catalogueIcontwo' @click="isLike()">
              <img v-if="!showWD" src="../../assets/image/文档我喜欢.png" alt="" class='catalogueIcontwo' @click="isLike()">
            </div>
            <!-- <div class="icon-wrapper">
                <span class="icon-a" id="fonta" @click="showSetting(0)" style="color:#384150; font-size: 0.4rem; padding: 0.2rem;">A</span>
            </div> -->
          </div>

        </div>
        <!--显示几个笔记 -->
        <div class="menu-wrapper menu-wrapper-bj" :class="showTy?'input_node-white':'input_node-black'" v-if="isShowBj === 2" >
            <div class="menu-wrapper-bjCount" @click="bjHit()">{{bjList.length}}个笔记</div>
        </div>
        <div class="setting-wrapper" id="font" style="display: none;">
            <div class="setting-font-size" v-if="showTag===0">
                <div class="preview" :style="{fontSize: fontSizeList[0].fontSize + 'px'}">A</div>
                <div class="select">
                    <div class="select-wrapper" v-for="(item, index) in fontSizeList" :key="index"
                         @click="setFontSize(item.fontSize)">
                        <div class="line"></div>
                        <div class="point-wrapper">
                            <div class="point"
                                 v-if="defaultFontSize === item.fontSize">
                                <div class="small-point"></div>
                            </div>
                        </div>
                        <div class="line"></div>
                    </div>
                </div>
                <div class="preview" :style="{fontSize: fontSizeList[fontSizeList.length - 1].fontSize + 'px'}">A</div>
            </div>
            <div class="setting-theme" v-if="showTag===1" id="themes">
                <div class="setting-theme-item" v-for="(item, index) in themeList" @click="setTheme(index)" :key="index">
                    <div class="preview" :style="{background: item.style.body.background}"
                         :class="{'no-border': item.style.body.background !== '#fff'}"></div>
                    <div class="text" :class="{'selected': index === defaultTheme}">{{item.name}}</div>
                </div>
            </div>
            <div class="setting-progress" v-if="showTag===2">
                <div class="progress-wrapper">
                    <input class="progress" type="range" max="100" min="0" step="0.01"
                           @change="onProgressChange($event.target.value, 1)"
                           @input="onProgressInput($event.target.value)" :value="progress"
                           :disabled="!bookAvailable" ref="progress">
                </div>
                <div class="text-wrapper">
                    <span style="font-size: 0.2rem">({{bookAvailable ? progress + '%' : '加载中...'}})</span>
                </div>
            </div>
        </div>
        <div class="content-mask" style="display: none">

            <div class="content-wrapper">
                <div class="title-menu">{{bookName}}</div>
                <!-- <div class="search-wrapper">
                    <div class="search">
                        <span class="iconfont icon-search span-search"></span>
                        <input style="-webkit-user-select: auto;" class="input" id="key" @input="OnInput1" v-model="keyword" placeholder="搜索">
                        <button class="btn" @click="OnInput">{{button}}</button>
                    </div>
                </div> -->
                <div class="clicke" @click="hideContent()">
                    <div v-if="isOne" @click="jumpTo(item.href)" class="content-item" :class="showTy?'':'bj-input_node-black'"  v-for="(item, index) in navigation.toc" :key="index">
                        <span class="text">{{item.label}}</span>
                    </div>
                    <!-- <div v-if="!showContent" class="note-item" v-for="(item, index) in searchList" :key="index">
                        <span class="text" @click="jumpTo1(item.cfi, true)" v-html="item.excerpt"></span>
                    </div> -->
                    <div v-if="!isOne" @click="jumpTo(item.cataNum,item.thePageNum)" class="content-item content-item-style" :class="showTy?'':'bj-input_node-black'"  v-for="(item, index) in bjAllList" :key="index">
                        <div class="bjtext">{{item.noteContent}}</div>
                        <div class="">{{item.createTime}}</div>
                    </div>
                </div>
                <div class="catalogue-bottom">
                  <div class="fontshow"  :class="{showColor:isOne}" @click="isOneSeclec(0)">目录</div>
                  <div style="width:0.02rem;height:0.34rem;background:rgba(186,191,197,0.45);"></div>
                  <div class="fontshow" :class="{showColor:!isOne}" @click="isOneSeclec(1)">笔记</div>
                </div>
            </div>

            <div class="n-wrapper" @click="onMaskClick"></div>
        </div>
    </div>
    <!-- 笔记列表-->
    <div class="bjList" v-show="ifShowBjList">
      <div class='bjList-head' @click="closeDtkBox()"></div>
      <div class="bjList-message">
        <div class="bjList-message-content" :class="showTy?'input_node-white':'input_node-black'">
          <div v-for="(item,index) in bjList" :key="index" style="margin-top:0.08rem">
            <div class="bjList-mes">
              <div>{{item.createTime}}</div>
              <div>
                <img @click="edit(item)" style="width:0.34rem;height:0.34rem;margin-right: 0.3rem;" src="../../assets/image/编辑@2x.png" >
                <img style="width:0.34rem;height:0.34rem;" src="../../assets/image/删除(1)@2x.png" @click="del(item)">
              </div>
            </div>
            <div class="bjList-noteContent">{{item.noteContent}}</div>
          </div>
        </div>
        <div :class="{colorClass: !showTy}" style="width: 100%;display: flex;justify-content: start;align-items: center;height: 1rem;">
          <img style="width:0.33rem;height:0.33rem;position: absolute;margin-left:0.34rem" src="../../assets/image/记录@2x.png">
          <span style="font-size:0.28rem;color:rgba(159,167,175,0.8);margin-left:0.81rem;" @click="editAdd()"> 记笔记...</span>
        </div>
      </div>
    </div>
    <!-- 加载页面 -->
    <Loading :dialogVisible="verDialogVisible"></Loading>
     <!-- 添加笔记 -->
    <AddBj :bjDialogVisible="AddBjDialogVisible"  :messAll="messAll" :showTy="showTy" :isEdit="isEdit" @close='colseMes' @closeAll='colseAllMes'></AddBj>
    <!-- 夜间模式添加笔记 -->
    <AddBjBlack :dialogVisibleBlack="AddBjDialogVisibleBlack"  :messAll="messAll" :showTy="showTy" :isEdit="isEdit" @close='colseMes' @closeAll='colseAllMes'></AddBjBlack>
</div>
</template>

<script>
import API from '@/api/api'
import $ from 'jquery'
import '@/assets/js/epub.min.js'
import '@/assets/js/jszip.min.js'
import AddBj from './../components/addBj'
import AddBjBlack from './../components/addBjBlack'
import Loading from './../components/loading'
import { debuglog } from 'util';
import { pipeline } from 'stream';
// import reader from '../../utils/reader'
export default {
  name: 'index',
  components: { Loading , AddBj, AddBjBlack},
  data () {
    return {
      verDialogVisible: true, // 加载页
      showWD: true, // 我喜欢
      showTy: true, // 夜间模式调整
      fontSizeList: [
        { fontSize: 12 },
        { fontSize: 14 },
        { fontSize: 16 },
        { fontSize: 18 },
        { fontSize: 20 },
        { fontSize: 22 },
        { fontSize: 24 }
      ], // 字体大小选择
      showContent: true, // 判断是否显示目录列表和搜索结果, true为目录
      button: '搜索',
      defaultFontSize: 16, // 字体大小默认
      themeList: [
        {
          name: '默认',
          style: {
            body: {
              'color': '#000', 'background': '#fff'
            }
          }
        },
        {
          name: '护眼',
          style: {
            body: {
              'color': '#000', 'background': '#ceeaba'
            }
          }
        },
        {
          name: '夜间',
          style: {
            body: {
              'color': '#fff', 'background': '#000'
            }
          }
        },
        {
          name: '雅致',
          style: {
            body: {
              'color': '#000', 'background': 'rgb(241, 236, 226)'
            }
          }
        }
      ], // 主题可选择类型
      reader: null,
      defaultTheme: 0, // 背景主题默认
      showTag: 0, // 展示主题进度字体
      flag: false, // 主题字体是否显示
      // 获取Locations对象
      // 通过epub.js的钩子函数实现
      locations: '',
      location: '',
      // 图书是否处于可用状态
      bookAvailable: false,
      progress: 0,
      book: '',
      rendition: '',
      navigation: '', // 目录
      isShowContent: false, // 目录是否在显示状态
      isShowNote: false, // 判断书签列表是否显示
      keyword: '', // 搜索框内容
      touchStartX: '', // 鼠标刚触碰到屏幕时的x坐标
      touchStartY: '',
      touchStartTime: '', // 触碰的时间
      section: 0, // 章节
      firstOffsetY: 0,
      searchList: [], // 搜索结果列表
      offsetY: 0,
      isBookMask: false, // 是否显示书签列表栏
      bookmark: [], // 获取的书签列表
      bookName: '', // 根据书名存取书签进度等数据
      winHeight: '', // 屏幕高度(不同手机)
      touch: false, // 判断是否为双手触碰
      pixel: 0,
      timer: '',
      timeCount: 1,
      isBonusMax: false, // 是否积分上限
      trainMateId: '', // 文档id
      bookurl: '', // 书本路径
      isOne: true, // 显示目录或者笔记
      startTime: 0, // 开始阅读时间
      endTime: 0, // 结束阅读时间
      specialCode: '', // 专项类型
      tenfenzhong: 0, // 阅读时间
      isShowBj: 0 , // 0不显示笔记目录和夜间模式等目录1:显示夜间模式2：显示笔记目录
      bjContent: '' , // 笔记内容
      AddBjDialogVisible: false, // 是否显示添加笔记模态框
      AddBjDialogVisibleBlack: false,
      messAll : '' , // 修改笔记传值，新增时传空
      isEdit: 0, // 0:添加 1：修改
      schedule : '' , // 进度百分度
      cataNum : '' , // 章节
      thePageNum : 0 , // 这一章 的第几页
      bjList : [] , // 笔记集合
      ifShowBjList : false , // 是否显示笔记列表
      id: '' , // 笔记id
      isCata: true , // 是目录或者笔记
      bjAllList : [] , // 所有笔记集合
      showTip: '请输入' , // 显示提示信息
    }
  },
  created () {
    this.specialCode = this.$route.params.specialCode
    this.bookName = this.$route.params.name
    this.trainMateId = this.$route.params.id
    this.findFavoriteByMateId()
    this.findMateFoundation()
    this.findAllBjList();
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  watch: {
    tenfenzhong (val) {
      if (val !== this.startTime) {
        if (val % 600 === 0) {
          this.addSelfCredits()
        }
      }
    },
    showTy (val) {
      if (val) {
        // eslint-disable-next-line no-undef
        StatusBar.backgroundColorByHexString('#fff')
        // eslint-disable-next-line no-undef
        StatusBar.styleDefault()
      } else {
        // eslint-disable-next-line no-undef
        StatusBar.backgroundColorByHexString('#000')
        // eslint-disable-next-line no-undef
        StatusBar.styleLightContent()
      }
    }
  },
  methods: {
    /**
     * 获取所有笔记集合
     */
    findAllBjList(){
      API.post('findAllSelfNotesAndAllCata', {
        trainMateId: this.trainMateId,
      }, 'json', (data, err) => {
        if (data.success) {
          this.bjAllList = data.data;
          for (var i = 0; i < data.data.length; i++) {
            this.bjAllList[i].createTime = this.GLOBAL.timestampToDate(data.data[i].createTime)
          }
        }
      })
    },
    /**
     * 笔记的修改
     */
    edit (item) {
      this.id = item.id;
      this.messAll = item.noteContent
      this.isEdit = 1
      this.ifShowBjList = false;
       if (this.showTy) {
        this.AddBjDialogVisible = true;
      }else {
        this.AddBjDialogVisibleBlack = true;
      }
    },
    /**
     * 删除笔记
     */
    del (item) {
      API.post('deleteSelfNotes', {
        id: item.id
      }, 'json', (data, err) => {
        if (data.success) {
          this.selectSelfNotes()
        }
      })
    },
    /**
     * 点击笔记
     */
    bjHit(){
      if (this.bjList.length <= 0) {
        return
      }
      this.ifShowBjList = true;
      this.isShowBj = 1;
    },
    // 关闭笔记列表
    closeDtkBox () {
      this.ifShowBjList = false;
      this.toggleShow();
    },
    colseAllMes(data){
      this.AddBjDialogVisible = false;
      this.AddBjDialogVisibleBlack = false;
      this.messAll = data;
      // let u = navigator.userAgent // 判断是安卓还是ios
      // let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
      // let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
      // if (isIOS) {
      //   cordova.plugins.Keyboard.close();
      // }
    },
    colseMes(data){
      // let u = navigator.userAgent // 判断是安卓还是ios
      // let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
      // let isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
      // if (isIOS) {
      //   cordova.plugins.Keyboard.close();
      // }
      this.messAll = data
      if (data.trim() === '') {
        return
      }
      this.toggleShow()
      if (this.isEdit === 0) {
        API.post('addSelfNotes', {
          trainMateId: this.trainMateId,
          schedule: this.schedule,
          noteContent: this.messAll,
          cataNum: this.cataNum,
          thePageNum: this.thePageNum,
        }, 'json', (data, err) => {
          if (data.success) {
            this.AddBjDialogVisible = false;
            this.AddBjDialogVisibleBlack = false;
            if (this.isEdit === 0) {
              this.ifShowBjList = false;
            }else {
              this.ifShowBjList = true;
            }
            this.messAll = '';
            this.selectSelfNotes()
          }
        })
        return
      }

      this.AddBjDialogVisible = false
      API.post('updateSelfNotes', {
        id: this.id,
        noteContent: this.messAll
      }, 'json', (data, err) => {
        if (data.success) {
          this.AddBjDialogVisible = false;
          this.AddBjDialogVisibleBlack = false;
          this.messAll = '';
          this.ifShowBjList = true;
          if (this.isEdit === 0) {
              this.ifShowBjList = false;
          }else {
            this.ifShowBjList = true;
          }
          this.selectSelfNotes();
          this.isShowBj = 1;
        }
      })
    },
    // 获取笔记
    selectSelfNotes () {
      API.post('selectSelfNotes', {
        trainMateId: this.trainMateId,
        schedule: this.schedule,
        cataNum : this.cataNum ,
        thePageNum : this.thePageNum
      }, 'json', (data, err) => {
        if (data.success) {
          this.bjList = []
          this.bjList = data.data
          if (data.data.length === 0) {
            this.ifShowBjList = false;
          }
          this.showTip = data.data.length >= 10 ? '已到上限10条':'请输入';
          for (var i = 0; i < data.data.length; i++) {
            this.bjList[i].createTime = this.GLOBAL.timestampToTime(data.data[i].createTime)
          }
        }
      })
    },
    /**
     * 点击新增笔记
     */
    showAddBjModel(){
      if (this.bjList.length >= 10) {
        return
      }
      this.isEdit = 0;
      this.ifShowBjList = false;
      if (this.showTy) {
        this.AddBjDialogVisible = true;
      }else {
        this.AddBjDialogVisibleBlack = true;
      }
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    tenfen () {
      setInterval(() => {
        this.tenfenzhong++
      }, 1000)
      // setTimeout(() => {
      //   this.tenfenzhong = this.tenfenzhong + 1
      //   this.tenfen()
      // }, 1000)
    },
    // 阅读增加积分
    addSelfCredits () {
      API.post('addSelfCredits', {
        specialCode: this.specialCode,
        creditsId: 'learnTime'
      }, 'json', (data, err) => {
        if (data.success) {
        }
      })
    },
    // 查询该文档是否标记了喜欢
    findFavoriteByMateId () {
      API.post('findFavoriteByMateId', {
        trainMateId: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data === 1) {
            this.showWD = false
          } else {
            this.showWD = true
          }
        }
      })
    },
    isLike () {
      this.showWD = !this.showWD
      this.isLikeBook()
    },
    opentwo (item) {
      this.setTheme(item)
      this.showTy = !this.showTy
    },
    isLikeBook () {
      var isLikeMes = 0
      if (this.showWD) {
        isLikeMes = 0
      } else {
        isLikeMes = 1
      }
      API.post('addFavorite', {
        trainMateId: this.trainMateId,
        favo: isLikeMes
      }, 'json', (data, err) => {

      })
    },
    // 返回上一级
    back () {
      // this.isLikeBook()
      if (this.startTime === 0) {
        this.endTime = 0
      } else {
        this.endTime = new Date().getTime()
      }
      API.post('save', {
        contentId: '',
        pagination: '',
        trainMateId: this.trainMateId,
        proportion: 0,
        studyTime: this.endTime - this.startTime
      }, 'json', (data, err) => {
        if (data.success) {
          this.$router.back()
        } else {
          this.$toast(data.msg)
          this.$router.back()
        }
      })
    },
    // 目录和笔记
    isOneSeclec (item) {
      // this.mlorbj = item
      if (item === 0) {
        this.isOne = true
        this.isCata = true
        // this.showMes = true
      } else {
        this.findAllBjList();
        this.isOne = false
        this.isCata = false
        // this.findAllSelfNotes()
        // this.showMes = false
      }
    },
    refreshLocation (startCfi) { // 跳转翻页时刷新定位,用来判断是否已添加书签
      if (this.bookmark) {
        if (this.bookmark.some(item => item.cfi === startCfi)) {
          this.isBookMask = true
          $('.ebook-icon-wrapper').css('display', 'block')
          $('.icon-added').css('color', 'red')
        } else {
          this.isBookMask = false
          $('.ebook-icon-wrapper').css('display', 'none')
          $('.icon-added').css('color', '#384150')
        }
      } else {
        this.isBookMask = false
        $('.ebook-icon-wrapper').css('display', 'none')
        $('.icon-added').css('color', '#384150')
      }
    },
    addNote () { // 点击添加书签,通过截取当前页的开始与结束的定位判断给哪一页添加书签
      if (!this.isBookMask) {
        var currentLocation = this.book.rendition.currentLocation()
        var cfibase = currentLocation.start.cfi.replace(/!.*/, '')
        var cfistart = currentLocation.start.cfi.replace(/.*!/, '').replace(/\)$/, '')
        var cfiend = currentLocation.end.cfi.replace(/.*!/, '').replace(/\)$/, '')
        var cfirange = `${cfibase}!,${cfistart},${cfiend})`
        this.book.getRange(cfirange).then(range => {
          var text = range.toString().replace(/\s\s/g, '')
          this.bookmark.push({
            cfi: currentLocation.start.cfi,
            text: text
          })
          localStorage.setItem(this.bookName, JSON.stringify(this.bookmark))
          this.isBookMask = true
          // alertMsg('书签添加成功')
          this.$toast('书签添加成功')
          $('.ebook-icon-wrapper').css('display', 'block')
          $('.icon-added').css('color', 'red')
        })
      } else {
        // var currentLocation = this.book.rendition.currentLocation()
        var cfi = currentLocation.start.cfi
        this.bookmark = $.parseJSON(localStorage.getItem(this.bookName))
        this.bookmark = this.bookmark.filter(item => item.cfi !== cfi)
        localStorage.setItem(this.bookName, JSON.stringify(this.bookmark))
        this.isBookMask = false
        this.$toast('书签取消成功')
        // alertMsg('书签取消成功')
        $('.ebook-icon-wrapper').css('display', 'none')
        $('.icon-added').css('color', '#384150')
      }
    },
    move (e) {
      // var offsetY = 0;
      if (this.firstOffsetY) {
        this.offsetY = e.changedTouches[0].clientY - this.firstOffsetY
      } else {
        this.firstOffsetY = e.changedTouches[0].clientY
      }
      e.preventDefault()
      e.stopPropagation()
    },
    moveEnd (e) {
      this.firstOffsetY = 0
    },
    onMaskClick (e) {
      var offsetX = e.offsetX
      var wid = window.innerWidth
      if (offsetX > 0 && offsetX > wid * 0.7) {
        this.hideContent()
      }
    },
    search () { // 全文搜索
      if (this.keyword) {
        if (this.button === '取消') {
          $('.title-menu').html('目录')
          this.button = '搜索'
          this.showContent = true
          this.keyword = ''
        } else {
          $('.title-menu').html('搜索结果')
          this.button = '取消'
          this.showContent = false
          if (this.keyword && this.keyword.length > 0) {
            this.doSearch(this.keyword).then(list => {
              this.searchList = list
              this.searchList.map(item => {
                item.excerpt = item.excerpt.replace(this.keyword, `<span style="color: yellow;">${this.keyword}</span>`)
                var a, b
                var before = item.excerpt.split(this.keyword)[0]
                if (before.length - 30 >= 10) {
                  b = before.substring(before.length - 40)
                } else if (before.length >= 0 && before.length - 30 < 10) {
                  b = before
                }
                var after = item.excerpt.split(this.keyword)[1]
                if (after.length - 6 >= 20) {
                  a = after.substring(0, 25)
                } else if (after.length >= 0 && after.length - 6 < 20) {
                  a = after
                }
                item.excerpt = b + `<span style="color: yellow;">${this.keyword}</span>` + a
                return item
              })
            })
          } else {
            this.searchList = ''
          }
        }
      } else {
        $('.title-menu').html('目录')
        this.button = '搜索'
        this.showContent = true
        this.keyword = ''
      }
    },
    doSearch (q) { // 调用epubjs内的搜索 可进行全文搜索
      return Promise.all(
        this.book.spine.spineItems.map(item => item.load(this.book.load.bind(this.book)).then(item.find.bind(item, q)).finally(item.unload.bind(item)))
      ).then(results => Promise.resolve([].concat.apply([], results)))
    },
    hideContent () {
      this.isShowContent = false
      if (!this.isShowContent) {
        $('.content-mask').toggle(200)
      }
    },
    // hideNote: function () {
    //     this.isShowNote = false;
    //     if (!this.isShowNote) {
    //         $(".search-list-wrapper").toggle(200);
    //     }
    // },
    hideNotes () {
      this.isShowNote = false
      if (!this.isShowNote) {
        $('.note-wrapper').toggle(200)
      }
      // this.searchList = "";
      // this.keyword = "";
    },
    // 根据链接跳转到指定位置
    jumpTo (href, thePageNum) {
      this.rendition.display(href,thePageNum).then(() => {
        var currentLocation = this.rendition.currentLocation()
        var cfi = currentLocation.end.cfi
        this.refreshLocation(cfi)
        var progress = Math.floor(((this.locations.percentageFromCfi(currentLocation.start.cfi)).toFixed(5)) * 10000) / 100
        this.progress = progress
        this.onProgressInput(progress)
      })
      this.rendition.display(href)
      // this.toggleShow();
    },
    jumpTo1 (href, highlight = false) {
      this.rendition.display(href)
      this.toggleShow()
    },
    jumpTo2 (href) {
      this.rendition.display(href).then(() => {
        var currentLocation = this.rendition.currentLocation()
        var cfi = currentLocation.start.cfi
        this.refreshLocation(cfi)
      })
      // this.toggleShow();
    },
    setTheme (index) { // 设置主题背景色
      this.rendition.themes.select(this.themeList[index].name)
      this.defaultTheme = index
      console.log(index);
      
      switch (index) { // 根据不同背景色给目录显示地方修改相应背景色
        case 0:
          $('.center').css('color', '#384150')
          $('.iconfont').css('color', '#384150')
          $('.icon-a').css('color', '#384150')
          $('.content-wrapper').css('background', '#fff')
          $('.content-item').css('color', '#000')
          $('.title-menu').css('color', '#000')
          $('.search-wrapper').css('background', '#fff')
          $('.search').css('color', '#999')
          $('.title-wrapper').css('background', '#fff')
          $('.menu-wrapper').css('background', '#fff')
          $('.input_node').css('background', '#fff')
          $('.read-wrapper').css('background', '#fff')
          break
        case 1:
          $('.content-wrapper').css('background', '#ceeaba')
          $('.content-item').css('color', '#000')
          $('.title-menu').css('color', '#000')
          $('.search-wrapper').css('background', '#ceeaba')
          $('.search').css('color', '#999')
          $('.title-wrapper').css('background', '#ceeaba')
          break
        case 2:
          $('.center').css('color', '#fff')
          $('.iconfont').css('color', '#fff')
          $('.icon-a').css('color', '#fff')
          $('.content-wrapper').css('background', '#000')
          $('.content-item').css('color', '#fff')
          $('.title-menu').css('color', '#fff')
          $('.search-wrapper').css('background', '#000')
          $('.search').css('color', '#fff')
          $('.title-wrapper').css('background', '#000')
          $('.menu-wrapper').css('background', '#000')
          $('.input_node').css('background', '#000')
          $('.read-wrapper').css('background', '#000')
          break
        case 3:
          $('.content-wrapper').css('background', 'rgb(241, 236, 226)')
          $('.content-item').css('color', '#000')
          $('.title-menu').css('color', '#000')
          $('.search-wrapper').css('background', 'rgb(241, 236, 226)')
          $('.search').css('color', '#999')
          $('.title-wrapper').css('background', 'rgb(241, 236, 226)')
        case 4:
        debugger
          $('.center').css('color', '#fff')
          break
      }
    },
    findMateFoundation () {
      API.post('findMateFoundation', {
        id: this.trainMateId
      }, 'json', (data, err) => {
        if (data.success) {
          this.bookurl = data.data.url
          this.progress = data.data.selfTrainEntity.schedule
          this.registerBook()
        }
      })
    },
    registerBook () { // 渲染电子书,目录,分页等操作
      // this.reader = reader
      this.book = ePub(this.bookurl)
      this.rendition = this.book.renderTo('reader', {
        width: window.innerWidth,
        height: document.documentElement.clientHeight
        // flow: 'auto'
      })
      // $(".mask").css("display", "flex");
      if (localStorage.getItem(this.bookName + 'progress')) {
        var tar = JSON.parse(localStorage.getItem(this.bookName + 'progress'))
        this.book.rendition.display(tar.start.cfi)
      } else {
        localStorage.progress = 0
        this.rendition.display()
      }
      this.rendition.display()
      this.rendition.on('mouseup', event => {

      })
      this.rendition.on('touchstart', event => {
        if (!this.showTy) {
          console.log(111);
          
           $('.center').css('color', '#fff')
        }
        var len = event.touches.length
        this.touchStartX = event.changedTouches[0].clientX
        this.touchStartY = event.changedTouches[0].clientY
        if (len === 1) {
          this.touchStartTime = event.timeStamp
          if (this.pixel > 1 && this.pixel < 1.1) {
            this.$toast('请缩放到原始翻页')
          }
        } else {
          this.touch = true
        }
      })
      this.rendition.on('touchend', event => {
        this.cataNum = this.rendition.location.end.cfi;
        this.thePageNum = this.rendition.location.end.displayed.page;
        this.schedule = this.rendition.location.end.percentage;
        this.selectSelfNotes();
        var offsetX = event.changedTouches[0].clientX - this.touchStartX// 根据手离开屏幕和触碰屏幕的正负判断
        var offsetY = event.changedTouches[0].clientY - this.touchStartY
        // if (!this.touch) {
          var time = event.timeStamp - this.touchStartTime// 根据在屏幕点击的时间判断是否翻页
          if (time < 180 && offsetX < -15 && Math.abs(offsetY) <= Math.abs(offsetX)) {
            this.nextPage()
          } else if (time < 180 && offsetX > 15 && Math.abs(offsetY) <= Math.abs(offsetX)) {
            this.prevPage()
          } else {
            // 显示上下菜单
            this.toggleShow()
          }
        // } else if (this.touch) {
        //   this.$toast('单手指滑动')
        //   this.pixel = window.visualViewport.scale
        //   if (this.pixel > 1) {
        //     $('#prev').hide()
        //     $('#next').hide()
        //     // this.touch = true;
        //   } else if (this.pixel === 1) {
        //     $('#prev').show()
        //     $('#next').show()
        //     this.touch = false
        //     // alertMsg("已是原始比例");
        //   }
        // }
        event.preventDefault
        event.stopPropagation()
      })
      this.book.ready.then(() => {
        this.navigation = this.book.navigation// 目录
        // 执行分页
        return this.book.locations.generate(750 * (window.innerWidth / 375))// 获取进度前需要执行分页
      }).then(result => {
        // 获取locations对象
        this.locations = this.book.locations
        if (localStorage.getItem(this.bookName)) {
          this.bookmark = $.parseJSON(localStorage.getItem(this.bookName))
          var currentLocation = this.rendition.currentLocation()
          var cfi = currentLocation.start.cfi
          this.refreshLocation(cfi)
        }
        this.bookAvailable = true
        // var pross = localStorage.progress
        // localStorage.progress = 50
        // var pross = 50
        var pross = this.progress
        this.onProgressChange(pross, 1)
      })

      this.setFontSize(this.defaultFontSize)
      this.themeList.forEach(theme => {
        this.rendition.themes.register(theme.name, theme.style)
      })
      this.startTime = new Date().getTime();
    },
    // progress 进度条的数值(0-100)
    onProgressChange (progress, tb) {
      this.progress = progress
      var percentage = progress / 100
      this.location = percentage > 0 ? this.locations.cfiFromPercentage(percentage) : 0
      if (tb === 1) {
        this.rendition.display(this.location)
      }
      this.refreshLocation(this.location)
      this.verDialogVisible = false
      this.tenfen()
    },
    onProgressInput (progress) {
      var percentage = progress / 100
      this.location = percentage > 0 ? this.locations.cfiFromPercentage(percentage) : 0
      this.progress = progress
      // this.$refs.progress.style.backgroundSize = `${progress}% 100%`
    },
    prevPage () { // 上一页
      this.rendition.prev().then(() => {
        $('.mint-toast').css('display', 'none')
        this.hideTitleAndMenu()
        var currentLocation = this.rendition.currentLocation()
        var cfi = currentLocation.start.cfi
        this.refreshLocation(cfi)
        this.selectSelfNotes();
        var progress = Math.floor(((this.locations.percentageFromCfi(currentLocation.start.cfi)).toFixed(5)) * 10000) / 100
        this.progress = progress
        this.onProgressInput(progress)
      })
    },
    nextPage: function () { // 下一页
      this.rendition.next().then(() => {
        this.hideTitleAndMenu()
        var currentLocation = this.rendition.currentLocation()
        this.cataNum = this.rendition.location.end.cfi;
        this.thePageNum = this.rendition.location.end.displayed.page;
        this.schedule = this.rendition.location.end.percentage;
        var cfi = currentLocation.start.cfi

        var progress = Math.floor(((this.locations.percentageFromCfi(currentLocation.start.cfi)).toFixed(5)) * 10000) / 100
        this.refreshLocation(cfi)
        this.selectSelfNotes();
        this.progress = progress
        this.onProgressInput(progress)
        if (currentLocation.atEnd) {
          this.$toast('已是最后一页')
        }
      })
    },
    hideTitleAndMenu: function () { // 触发点击屏幕时 上下菜单栏隐藏显示
      $('.title-wrapper').hide(50)
      $('.menu-wrapper').hide(50)
      $('#font').hide(50)
      this.isShowBj = 0;
      this.flag = false
    },
    /**
     * 点击屏幕是否显示上面的文件名称和下面的目录等
     */
    toggleShow: function () {
      this.isShowBj = this.isShowBj === 0?1:(this.isShowBj === 1?2:1);
      // $('.title-wrapper').slideToggle(200)
      // $('.menu-wrapper').slideToggle(200, function () {
      //   if ($('.menu-wrapper').css('box-shadow') === 'none') {
      //     $('.menu-wrapper').css({ 'box-shadow': '0 -0.15rem 0.15rem rgba(0,0,0,0.15)' })
      //   }
      // })
      $('#font').hide(50)
      this.flag = false
    },
    OnInput: function () {
      this.search()
    },
    OnInput1: function () {
      if (this.keyword) {
        // $(".title-menu").html("目录");
        this.button = '搜索'
        // this.showContent = false;
      } else {
        $('.title-menu').html('目录')
        this.button = '搜索'
        this.showContent = true
      }
    },
    toggleNote: function () {
      this.isShowNote = true
      if (this.isShowNote) {
        $('.note-wrapper').toggle(200)
      }
      this.hideTitleAndMenu()
    },
    showSetting: function (tag) {
      this.findAllBjList();
      this.isOne = true;
      $('.mint-toast').css('display', 'none')
      if (this.flag === false) {
        if (tag === 3) {
          this.isShowContent = true
          if (this.isShowContent) {
            $('.content-mask').toggle(200)
          }
          this.hideTitleAndMenu()
        } else {
          $('.menu-wrapper').css('box-shadow') === 'none' ? $('.menu-wrapper').css({ 'box-shadow': '0 -0.15rem 0.15rem rgba(0,0,0,0.15)' }) : $('.menu-wrapper').css({ 'box-shadow': 'none' })
          $('#font').slideToggle(200)
          this.flag = true
          this.showTag = tag
        }
      } else {
        if (tag === 3) {
          this.isShowContent = true
          if (this.isShowContent) {
            $('.content-mask').toggle(200)
          }
          $('.menu-wrapper').css('box-shadow') === 'none' ? $('.menu-wrapper').css({ 'box-shadow': '0 -0.15rem 0.15rem rgba(0,0,0,0.15)' }) : $('.menu-wrapper').css({ 'box-shadow': 'none' })
          $('#font').slideToggle(200)
          this.hideTitleAndMenu()
          this.flag = false
        } else {
          if (this.showTag === tag) {
            $('.menu-wrapper').css('box-shadow') === 'none' ? $('.menu-wrapper').css({ 'box-shadow': '0 -0.15rem 0.15rem rgba(0,0,0,0.15)' }) : $('.menu-wrapper').css({ 'box-shadow': 'none' })
            $('#font').slideToggle(200)
            this.flag = false
          } else {
            this.showTag = tag
            this.flag = true
          }
        }
      }
    },
    // 设置字体大小
    setFontSize: function (fontSize) {
      this.defaultFontSize = fontSize
      // 设置Theme对象
      if (this.rendition.themes) {
        this.rendition.themes.fontSize(fontSize + 'px')
      }
    }

  }

}
</script>

<style scoped>
  .text_train{
    width:100%;
    height:100%;
  }
  .text_train .menu-wrapper-bjCount{
    display: flex;
    width: 1.3rem;
    height: 0.45rem;
    background: #AFB8C1;
    margin-left: 0.25rem;
    justify-content: center;
    align-items: center;
    border-radius: 20%;
    color: #fff;
  }
  .text_train .menu-wrapper-bj{
    height: 0.9rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .text_train .menu-wrapper-top-fb{
    width: 0.75rem;
    display: block;
    margin-right: 0.15rem;
  }
  .text_train .bjList{
    z-index: 99;
    width:100%;
    height:100%;
    position: fixed;
    top:0;
    left: 0;
  }
  .bjList-head{
    width:100%;
    position:absolute;
    top:0;
    left: 0;
    height:calc(100vh - 4.4rem);
    z-index:99;
    background: rgba(0,0,0,0.7)
  }
  .bjList-message{
      position: absolute;
      z-index: 500;
      background: rgba(255,255,255,1);
      bottom: 0;
      width: 100%;
      height: 8.8rem;
      /* overflow-y: scroll !important; */
  }
  .bjList-message-title{
      display: flex;
      align-items: center;
      justify-content:space-between;
      width: 100%;
      height: 0.98rem;
      border-bottom: 0.012rem solid #DDDDDD;
  }
  .bjList-message-content{
      width:100%;
      height: 7.8rem;
      overflow: scroll!important;
      /* -webkit-overflow-scrolling: touch; */
      padding:0 0.2rem;
      font-size:0.3rem;
       z-index: 600;
  }
  .bjList-message-content > div {
    z-index: 700;
  }
  .bjList-mes{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-top: 0.26rem;
  }
  .bjList-noteContent{
    color:rgba(110,116,126,1);
    font-size: 0.3rem;
    line-height:0.4rem;
    word-break: break-word;
  }
  .input_node-black{
    background: rgb(22, 22, 23);
    color: #fff !important;
  }
  .input_node-white{
     background: #fff;
  }
 
  .bj-input_node-black{
    color: #fff !important;
  }
  .bjtext{
    width:65%;
    font-size: 0.28rem;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    display: inline-block;
    line-height:0.45rem;
    padding-top:0.02rem;
  }
  .content-item-style{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  /* .arrow {
    position: fixed;
    top: 50%;
    margin-top: -32px;
    font-size: 64px;
    color: #E2E2E2;
    font-family: arial, sans-serif;
    font-weight: bold;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    text-decoration: none;
  }
  #prev {
      left: 0;
  }
  #next {
    right: 0;
  } */
</style>
<style lang="stylus">
    @import '../../assets/css/read.css'
    //@import '../../assets/css/public.css'
</style>



